/* Buttons */

.order-history-details-button-reorder {
  @extend .button-secondary;
  @extend .button-medium;
  @extend .button-generic;
  margin-bottom: $sc-margin-lv2;
}

.order-history-details-button-download-pdf {
  @extend .button-secondary;
  @extend .button-medium;
  @extend .button-generic;
  margin-bottom: $sc-margin-lv2;
}

.order-history-details-button-request-return {
  @extend .button-tertiary;
  @extend .button-medium;
  @extend .button-generic;
  margin-bottom: $sc-margin-lv2;
}

.order-history-details-button-view-invoice {
  @extend .button-tertiary;
  @extend .button-medium;
  @extend .button-generic;
}

.order-history-details-shipping-information {
  background: $sc-color-theme-background;
  margin-bottom: $sc-margin-lv4;
  padding: $sc-padding-lv4;
  margin-left: -$sc-margin-lv4;
  margin-right: -$sc-margin-lv4;

  @media (min-width: $screen-xs-min) {
    margin-left: 0;
    margin-right: 0;
  }
}

.order-history-details-info-card-container {
  @extend .row;
  overflow: hidden;
  margin-bottom: $sc-margin-lv4;
}

.order-history-details-info-card {
  @extend .col-xs-12;
  @extend .col-sm-6;
}

.order-history-details-info-card-info {
  @extend .info-card;
  width: 100%;
  font-size: $sc-font-size-m;
  .address-details-address-title {
    display: block;
    font-style: normal;
    font-weight: $sc-font-weight-bold;
    @extend .info-card-content;
  }

  p {
    font-style: normal;
    display: block;
  }

  .address-details-container-multiselect-address-title {
    font-style: normal;
    text-transform: capitalize;
  }

  .address-details-container-multiselect-address-city {
    display: inline;
  }

  .address-details-container-multiselect-address-state {
    display: inline;
  }

  .address-details-container-multiselect-address-zip {
    display: inline;
  }
}

.order-history-details-info-card-info-content {
  @extend .info-card-content;
}

.order-history-details-accordion-divider {
  @extend .accordion-divider;
}

.item-view-actionable-selected-option-cell {
  padding-left: $sc-padding-lv3;
}

.order-history-details-payment-transactions {
  display: block;
  clear: both;
  padding-bottom: $sc-margin-lv3;
  margin-left: $sc-margin-lv3;
  margin-right: $sc-margin-lv3;
}

.order-history-details-payment-others {
  display: block;
  clear: both;
  padding-bottom: $sc-margin-lv3;
}

.order-history-details-payment-info-cards {
  @extend .row;
  padding-bottom: $sc-margin-lv3;
  overflow: hidden;
}

.order-history-details-header-amount-info {
  @media (min-width: $screen-sm-min) {
    display: none;
  }
}

.order-history-details-total-formatted {
  display: none;
  @media (min-width: $screen-sm-min) {
    display: inline-block;
  }
}

.order-history-details-payment-transactions-table {
  @extend .table-flex-item-navigable;

  thead {
    display: none;
    width: 100%;
    border-bottom: 1px solid $sc-color-theme-border-light;

    @media (min-width: $screen-md-min) {
      display: table-row-group;
    }
  }
  th {
    font-weight: 400;
  }
}

.order-history-details-payment-transactions-table-body-payment-transaction {
  @extend .sc-value;
}

.order-history-details-payment-transactions-table-header-amount {
