/* Buttons */

.receipt-details-button-reorder {
  @extend .button-secondary;
  @extend .button-medium;
  @extend .button-generic;
  margin-bottom: $sc-margin-lv2;
}

.receipt-details-button-download-pdf {
  @extend .button-secondary;
  @extend .button-medium;
  @extend .button-generic;
  margin-bottom: $sc-margin-lv2;
}

.receipt-details-button-request-return {
  @extend .button-tertiary;
  @extend .button-generic;
}

.receipt-details-shipping-information {
  background: $sc-color-theme-background;
  margin-bottom: $sc-margin-lv4;
  padding: $sc-padding-lv4;
}

.receipt-details-info-card-container {
  @extend .row;
  overflow: hidden;
  margin-bottom: $sc-margin-lv4;
}

.receipt-details-info-card {
  @extend .col-xs-12;
  @extend .col-sm-6;
}

.receipt-details-info-card-info {
  @extend .info-card;
  width: 100%;
  font-size: $sc-font-size-m;
  .address-details-address-title {
    display: block;
    font-style: normal;
    font-weight: $sc-font-weight-bold;
    @extend .info-card-content;
  }
  span {
    font-style: normal;
    display: block;
  }

  .address-details-city {
    display: inline;
  }

  .address-details-state {
    display: inline;
  }

  .address-details-zip {
    display: inline;
  }
}

.receipt-details-info-card-info-content {
  @extend .info-card-content;
}

.receipt-details-payment-transactions {
  display: block;
}

.receipt-details-item-summary-label {
  @extend .sc-label;
}

.receipt-details-total-formatted {
  display: none;
  @media (min-width: $screen-sm-min) {
    display: inline-block;
  }
}

.receipt-details-title-header-amount {
  display: none;
  float: right;

  @media (min-width: $screen-sm-min) {
    display: block;
  }
}

.receipt-details-accordion-divider {
  @extend .accordion-divider;
}

.receipt-details-accordion-container {
